import { ComponentStylingModel, ComponentStylingPropertiesControlComponent } from '@anglr/dynamic/layout-editor';
import { codePropertiesControlFor } from '@anglr/dynamic/layout-editor';
import { HtmlLanguageModel } from '@anglr/dynamic';
import { HtmlBlockModel } from './htmlBlock.model';
/**
 * Html block layout metadata
 */

export class HtmlBlockLayoutEditorMetadata {
  //######################### constructor #########################
  constructor() {
    //######################### public properties - implementation of LayoutEditorMetadataDescriptor #########################

    /**
     * @inheritdoc
     */
    this.metaInfo = {
      name: 'Html block',
      description: 'Html block - displays html or string',
      group: 'Data',
      optionsMetadata: {
        propertiesMetadata: [{
          modelType: ComponentStylingModel,
          propertiesControls: [ComponentStylingPropertiesControlComponent]
        }, {
          modelType: HtmlBlockModel,
          propertiesControls: [codePropertiesControlFor('content', HtmlLanguageModel)]
        }]
      },
      defaultOptions: {
        content: '<div>This is your <strong>HTML</strong> block</div>'
      }
    };
    Object.freeze(this);
  }

}